
const SEND_MESSAGE = "SEND_MESSAGE";

const initialState = {
  dialogsData: [
    { id: 1, name: "" },
    { id: 2, name: "" },
    { id: 3, name: "" },
    { id: 4, name: "" },
    { id: 5, name: "" },
    { id: 6, name: "" },
  ],
  messagesData: [
    { id: 1, message: "" },
    { id: 2, message: " ?" },
    { id: 3, message: " " },
    { id: 4, message: " ," },
    { id: 5, message: " " },
    { id: 6, message: "" },
  ],
  answersData: [
    { id: 1, answer: "" },
    { id: 2, answer: " ?" },
    { id: 3, answer: " " },
    { id: 4, answer: " ," },
    { id: 5, answer: " " },
    { id: 6, answer: "" },
  ],
};

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
 
    case SEND_MESSAGE:
      return {
        ...state,
        messagesData: [...state.messagesData, { id: 7, message: action.body }],
        newMessageBody:"",
      };
    default:
      return state;
  }
};

export const sendMessageAC = (body) => ({
  type: SEND_MESSAGE,
  body
})

export default dialogsReducer;
